<template>
  <div class="machine-learn">
    <div class="topTitle">
      <span class="topTitle-span1">机器学习</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">机器学习算法</span>
    </div>
    <div class="content" >
        <div class="content-detail" v-for="item in cardDatas">
            <div class="contentCellTitle">
                <div class="contentCellTitle-1"></div>{{item.name}}
            </div>
            <div class="details" v-for="cardData in item.cardData" @click="viewDetails(cardData.childType)">
                <img src="../../../assets/algol.png" alt="">
                <div class="wrapper">
                    <div class="title">{{cardData.name}}</div>
                    <div class="detail">
                        {{cardData.content}}
                    </div>
                </div>
            </div>
        </div>
    </div>
    <Modal :mask-closable="false"   v-model="showModal" title="算法介绍"   @on-cancel="cancel" class="dialog" :styles="{width: '70%'}" >
      <component :is="tabview"  width="80%" ></component>
      <div slot="footer"></div>
    </Modal> 
  </div>
</template>
<script>
import arithmeticDialog1 from '@/components/modelManage/machineLearnManage/arithmeticDialog/arithmeticDialog1'
import arithmeticDialog2 from '@/components/modelManage/machineLearnManage/arithmeticDialog/arithmeticDialog2'
import arithmeticDialog3 from '@/components/modelManage/machineLearnManage/arithmeticDialog/arithmeticDialog3'
import arithmeticDialog4 from '@/components/modelManage/machineLearnManage/arithmeticDialog/arithmeticDialog4'
import arithmeticDialog5 from '@/components/modelManage/machineLearnManage/arithmeticDialog/arithmeticDialog5'
import arithmeticDialog6 from '@/components/modelManage/machineLearnManage/arithmeticDialog/arithmeticDialog6'
import arithmeticDialog7 from '@/components/modelManage/machineLearnManage/arithmeticDialog/arithmeticDialog7'
import arithmeticDialog8 from '@/components/modelManage/machineLearnManage/arithmeticDialog/arithmeticDialog8'
import arithmeticDialog9 from '@/components/modelManage/machineLearnManage/arithmeticDialog/arithmeticDialog9'
export default {
    components:{
        arithmeticDialog1,
        arithmeticDialog2,
        arithmeticDialog3,
        arithmeticDialog4,
        arithmeticDialog5,
        arithmeticDialog6,
        arithmeticDialog7,
        arithmeticDialog8,
        arithmeticDialog9,
    },
    data(){
        return{
          showModal:false,
           tabview:'arithmeticDialog1',
           cardDatas:[
               {
                   fatherType:'1',
                   name:'二分类算法',
                   cardData:[
                       {
                           name:'逻辑回归-二分类',
                           content:'LR（Logistic Regression）逻辑回归是业界常用的监督学习算法，常见用于对类别的预测，可以完成高维度，大样本数据下的快速建模场景',
                           childType:'1',
                       },
                       {
                           name:'随机森林-二分类',
                           content:'随机森林 (Random Forest) 是一种基于bagging思想的决策树算法。随机森林模型由多棵决策树组成，通过将多棵决策树预测结果进行投票，相比单个决策树有效降低了模型的方差（variance）',
                           childType:'2',
                       },
                       {
                           name:'朴素贝叶斯-二分类',
                           content:'NB是Naive Bayes的缩写，即朴素贝叶斯，它是基于贝叶斯定理与特征条件独立假设的分类方法。',
                           childType:'3',
                       },
                       {
                           name:'梯度提升决策树(GBDT)',
                           content:'LR（Logistic Regression）逻辑回归是业界常用的监督学习算法，常见用于对类别的预测，可以完成高维度，大样本数据下的快速建模场景',
                           childType:'4',
                       },
                       {
                           name:'支持向量机(SVM)',
                           content:'支持向量机（简称SVM）是一种经典的分类模型，分类学习最基本的思想就是基于某个训练集，在样本空间中找到一个划分超平面，将不同类别的样本分开',
                           childType:'5',
                       },

                   ],
               },
               {
                   fatherType:'2',
                   name:'多分类算法',
                   cardData:[
                       {
                           name:'逻辑回归-多分类',
                           content:'逻辑回归-多分类，是LR（Logistic regression）在多分类的推广，与LR一样，同属于广义线性模型',
                           childType:'6',
                       },
                   ],
               },
               {
                   fatherType:'3',
                   name:'回归算法',
                   cardData:[
                       {
                           name:'线性回归',
                           content:'线性回归（Linear regression）是回归分析中第一种经过严格研究并在实际应用中广泛使用的模型',
                           childType:'7',

                       },
                       {
                           name:'梯度提升回归树(GBRT)',
                           content:'GBRT(Gradient Boosted Regression Tree)，即梯度提升回归树，是一种基于迭代的回归树算法，其核心就在于，每一棵树是从之前所有树的残差中来学习的，适用于回归预测问题',
                           childType:'8',
                       }
                   ],
               },
               {
                   fatherType:'4',
                   name:'聚类算法',
                   cardData:[
                       {
                           name:'K-Means',
                           content:'K-Means是一种聚类分析算法，聚类是指试图将数据集中的样本划分为若干个通常是不相干的子集，每个子集称为一个“簇”，通过这样的划分，每个簇可能对应于一些潜在的类别',
                           childType:'9',
                       },
                   ],
               },
           ] 
        }
    },
    methods:{
        viewDetails(type){
            this.showModal = true;
            this.tabview = 'arithmeticDialog'+type;
            this.dataObj = {
                type
            }
        },
        cancel(){}
    }
};
</script>
<style lang="scss" scoped>
    .machine-learn{
        overflow: hidden;
        .contentCellTitle{
            font-size: 18px;
        }
        .content{
            .content-detail{
                margin: 0 -35px 0px 15px;
                overflow: hidden;
                .details{
                    padding: 30px 20px 10px 20px;
                    margin: 0 20px 20px 0px;
                    float: left;
                    width: 400px;
                    height: 220px;
                    background: #fff;
                    position: relative;
                    border-radius: 10px;
                    cursor: pointer;
                    img{
                        width: 60px;
                        height: 60px;
                    }
                    .wrapper{
                        display: inline-block;
                        vertical-align: top;
                        margin-left: 10px;
                        width: 280px;
                        .title{
                            font-size: 16px;
                            font-weight: bold;
                            color: #666666;
                            margin-bottom: 15px;
                        }
                        .detail{
                            font-size: 14px;
                            text-indent: 2em;
                            color: #8D8D8D;
                            max-height: 122px;
                            overflow: hidden;
                        }
                    }
                }
            }
        }
    }
</style>


